import { Notification } from 'element-ui';

export default function notifyMessage(type) {
    var boxName = "";
    switch (type) {
        case '1':
        case 1:
            boxName = "Wiki";
            break;
        case '2':
        case 2:
            boxName = "";
            break;
        case '3':
        case 3:
            boxName = "";
            break;
    }
    Notification.info({
        title: '',
        message: '' + boxName + ' ',
        duration: 0
    });
}


