import Vue from 'vue';
import Router from 'vue-router';


Vue.use(Router);

export default new Router({
    routes: [{
        path: '/',
        hidden: true,
        component: resolve => require(['../views/login/index.vue'], resolve)
    }, {
        path: '/login',
        hidden: true,
        component: resolve => require(['../views/login/index.vue'], resolve)
    }, {
        path: '/dashboard',
        component: resolve => require(['../views/layout/Layout.vue'], resolve),
        redirect: '/dashboard/index',
        noDropdown: true,
        icon: 'all',
        children: [{
            path: 'index',
            name: '',
            component: resolve => require(['../views/dashboard/index.vue'], resolve),
        }]
    }, {
        path: '/user',
        component: resolve => require(['../views/layout/Layout.vue'], resolve),
        name: '',
        redirect: '/user/wechat',
        icon: 'account',
        children: [{
            path: 'wechat',
            name: '',
            component: resolve => require(['../views/user/Wechat.vue'], resolve),
        }, {
            path: 'library',
            name: '',
            component: resolve => require(['../views/user/Library.vue'], resolve),
        }, {
            path: 'wiki',
            name: 'Wiki',
            component: resolve => require(['../views/user/Wiki.vue'], resolve),
        }]
    }, {
        path: '/message',
        component: resolve => require(['../views/layout/Layout.vue'], resolve),
        noDropdown: true,
        isMessageItem: true,
        icon: 'email',
        children: [{
            path: '',
            name: '',
            redirect: '/message/wiki',
        }, {
            path: 'detail/:id',
            name: '',
            component: resolve => require(['../views/message/MessageDetail.vue'], resolve),
        }, {
            path: ':name',
            name: '',
            //meta: { noBreadCrumb: true },
            component: resolve => require(['../views/message/index.vue'], resolve),
        }, ]
    }, {
        path: '/404',
        hidden: true,
        component: resolve => require(['../views/layout/Layout.vue'], resolve),
        children: [{
            path: '',
            component: resolve => require(['../views/error/404.vue'], resolve)
        }]
    }, {
        path: '*',
        hidden: true,
        redirect: '/404'
    }]
})


