import Vue from 'vue';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-default/index.css';
import App from './App.vue';
import NProgress from 'nprogress';
import 'nprogress/nprogress.css'; // NProgress
import router from './router';
import store from './store';
import { getToken } from './utils/auth.js';
require('promise.prototype.finally').shim();
import './assets/iconfont.js'; // iconfont
import IconSvg from './components/IconSvg.vue'; // svg 

Vue.use(ElementUI);
Vue.component('icon-svg', IconSvg);

// 
setInterval(() => {
    if(store.getters.token){
        store.dispatch("GET_MESSAGE_NUMBERS")
    }
}, 5000);


router.beforeEach((to, from, next) => {
    NProgress.start();
    // 
    if (getToken()) {
        // 
        if (store.getters.id == '') {
            store.dispatch('GET_INFO').then(() => {
                next();
            }).catch(() => {
                ElementUI.Message.error("");
                NProgress.done();
            });
        } else {
            NProgress.done();
            next();
        }
    } else {
        if (to.path != '/login') {
            next({ path: '/login' });
            NProgress.done();
        } else {
            next();
        }
    }
});

router.afterEach(() => {
    NProgress.done(); // Progress
});


new Vue({
    el: '#app',
    store,
    router,
    render: h => h(App)
});


